.img-content {
  position: relative;
}

.img-content img {
  position: absolute;
  width: 80%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content-col {
  padding: 7% 10%;
  box-sizing: border-box;
  text-align: center;
  background: #fff;
}

.heading {
  font-size: 50px;
  margin-bottom: 50px;
  text-transform: uppercase;
  color: #333333;
  font-weight: 900;
  letter-spacing: 2px;
}

.desc {
  font-size: 35px;
  color: #606060;
}
